import React from "react";
import './Function.css'

import CodeMirror from '@uiw/react-codemirror';
import 'codemirror/keymap/sublime';
import 'codemirror/theme/monokai.css';
import * as path from "path";


class Function extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            all_function_list: [],
            selected: null,
            test_run_res: null,
            path: null
        }
        this.code = 'def start():\n\treturn("123")\n\n\n\n\n\n\n\n';
    }

    onSelect(item) {
        console.log("select", item)
        fetch(this.backend_path + '/function/get?function_name=' + item)
            .then(res => res.json())
            .then(res => {
                this.setState({selected: res})
                console.log("get_function", res)
                document.getElementsByName("function_name")[0].value = res.name
                document.getElementsByName("code")[0].value = res.code

                for (let i of document.getElementsByName("language")) {
                    if (i.value === res.language) {
                        i.checked = true
                    }
                }
            })
    }

    function_test() {
        let name = document.getElementsByName("function_name")[0].value
        fetch(this.backend_path + '/function/test_run?function_name=' + name + '&param={}')
            .then(res => res.text())
            .then(res => {
                console.log("test_run", res)
                this.setState({test_run_res: res})
            })
    }


    onSave() {
        document.getElementsByName("code")[0].innerText = this.code
        console.log("onSave", this.code)

    }

    onCodeChange(code) {
        this.code = code.getValue()
    }


    render() {
        return (
            <div>
                <div className={"fn_main_frame"}>
                    <div className={"fn_left_bar"}>
                        {
                            this.state.all_function_list.map((i, j) => {
                                    return <div onClick={() => this.onSelect(i)}>{i}</div>
                                }
                            )
                        }
                    </div>

                    <div className={"fn_right_side"}>
                        <div className={"fn_edit_content"}>
                            <form className={"fn_content"} action={this.backend_path + "/function/create"}
                                  method={"post"} target="save_iframe" onSubmit={() => this.onSave()}>
                                <div>在此配置函数</div>
                                <div>函数名<input type={"text"} name={"function_name"}/>
                                </div>
                                <div>
                                    选择函数运行环境:
                                    <input type={"radio"} name={"language"} value={"python"}/> python
                                    <input type={"radio"} name={"language"} value={"node"}/> node
                                </div>

                                <div>
                                    选择函数运行配置:
                                    <input type={"radio"} name={"config"} value={"small"}/> 1c1g
                                </div>
                                <div>
                                    超时:
                                    <input type={"text"} name={"timeout"} value={"120"}/>s
                                </div>

                                <div>
                                    <div className={"fn_editor"}>
                                        <CodeMirror
                                            value={this.code}
                                            options={{
                                                theme: 'monokai',
                                                keyMap: 'sublime',
                                                mode: 'python'
                                            }}
                                            onChange={code => this.onCodeChange(code)}
                                        />
                                        <textarea className={"hidden_text_area"} name="code"/>
                                    </div>
                                </div>

                                <div>
                                    <input type={"submit"} value={"创建"} className={"fn_button"}/>
                                </div>
                                <div>
                                    <iframe name="save_iframe" className={"fn_save_iframe"} title={"save_iframe"}/>
                                </div>
                            </form>
                            <div>
                                <button onClick={() => this.function_test()} className={"fn_button"}>测试函数</button>
                            </div>
                            <div>
                                {this.state.test_run_res}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    componentDidMount() {
        fetch(this.backend_path + '/function/get_all')
            .then(res => res.text())
            .then(res => {
                res = res.substring(1, res.length - 1)
                res = res.replaceAll("'", "")
                res = res.replaceAll(" ", "")
                res = res.split(',')
                this.setState({all_function_list: res})
                console.log("get_all_function", res)
            })

    }
}

export default Function;
